.modal{
  display: -ms-flexbox;
  display: flex;
  position: fixed;
  background: rgba(0, 0, 0, 0.7);
  border: 1px solid red;
  height: 100%;
  width: 100%;
  -webkit-transform: translate(-50%,-50%);
      -ms-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  top: 50%;
  left: 50%;
  z-index: 2;
  /* right: 0;
  bottom: 0; */
  /* margin-left: auto;
  margin-right: auto; */
}

.inner-modal{
  position: relative;
  border: 1px solid white;
  height: 80%;
  width: 50%;
  margin: auto;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.inner-modal img {
  position: absolute;
  max-width:100%;
  min-height:100%;
  -o-object-fit: cover;
     object-fit: cover;
}

.exit{
  /* color:  */
  position: absolute;
  right: 0;
}

.exit span{
  display: block;
  color: white;
  font-size: 32px;
  padding: 20px 32px;
  cursor: pointer;
}

nav{
  /* background: #fff; */
  position: relative;
  margin: 0;
  padding: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  background-color: transparent;
}

section{
  height: 32rem;
  border: 1px solid;
}

.content{
  width: 37.5rem;
}


.banner{
  height: 500px;
  border: none;
  background: url(https://images.pexels.com/photos/713297/pexels-photo-713297.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260);
  background-size: cover;
  background-attachment: fixed, fixed;
  background-position: center, center;
  background-repeat: no-repeat; 
}

.links {
  color: white;
  text-decoration: none;
  padding: 1em;
  z-index: 1;
  background: #008081;
}

.links:hover{
  -webkit-box-shadow: inset 0 0 0 5px #fff;
          box-shadow: inset 0 0 0 5px #fff;
}

.links:focus{
  -webkit-box-shadow: inset 0 0 0 5px #fff;
          box-shadow: inset 0 0 0 5px #fff;
}

.banner-content {
  max-width: 70%;
  margin: auto;
  /* border: 1px solid red; */
}

.banner-content h1{
  color: white;
  font-size: 4rem;
  margin-bottom: 0;
  margin-top: 120px;
}

.banner-content p{
  margin-top: 0;
  color: white;
}

.calculator-button{
  background: #008081;
