/* gallery */

.gallery-title{
  background-color: white;
  max-width: 320px;
  margin: auto;
  border-radius: 5px;
  text-align: center;
}

.gallery-title h2 {
  padding: 15px;
  font-size: 28px;
  color: #008081;
}

.gallery {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
  padding: 35px 0px;
}

.gallery img {
  height: 400px;
  width: 400px;
  -o-object-fit: cover;
     object-fit: cover;
  margin: 5px;
  border-radius: 5px;
}

.gallery-image{
  position: relative;
}

.image-info{
  position: absolute;
  top: 5px;
  bottom: 8px;
  right: 5px;
  left: 5px;
  background: rgba(255, 255, 255, 0.8);
  color: #008081;
  cursor: pointer;
  border-radius: 5px;
}

.image-info h2 {
  text-align: center;
  margin-top: 30%;
}

.image-info p {
  padding: 10px;
}

.image-detail{
  background: #008081;
  width: 120px;
  padding: 1rem;
  color: white;
  border: 1px solid #008081;
  display: block;
  margin: auto;
  cursor: pointer;
}


